import * as React from "react"
import { createContext, useContext, useRef, useCallback, useEffect } from "react"

type IsCurrentCallback = (isCurrent: boolean) => void
export const NavigationTargetContext = createContext<{
    register: (fn: IsCurrentCallback) => void
    deregister: (fn: IsCurrentCallback) => void
}>({ register: () => {}, deregister: () => {} })

type Props = React.PropsWithChildren<{ isCurrent: boolean }>

/**
 * @internal
 *
 * Run the registered callback when the current navigation target changes
 * @param isCurrent If the ground node is present as the current target in the preview
 */
export const NavigationTargetWrapper = ({ isCurrent, children }: Props) => {
    const callbacksRef = useRef<Set<IsCurrentCallback>>(new Set())

    const register = useCallback((fn: IsCurrentCallback) => {
        callbacksRef.current.add(fn)
    }, [])

    const deregister = useCallback((fn: IsCurrentCallback) => {
        callbacksRef.current.delete(fn)
    }, [])

    const value = useRef({ register, deregister }).current

    useEffect(() => {
        callbacksRef.current.forEach(cb => cb(isCurrent))
    }, [isCurrent])

    return <NavigationTargetContext.Provider value={value}>{children}</NavigationTargetContext.Provider>
}

/**
 * @internal
 *
 * Register the callback on mount to the NavigationTargetWrapper, deregister it on unmount.
 * The callback will be fired when the current target changes.
 * @param callback A callback that receives the `isCurrent` state of the NavigateTargetWrapper
 */
export function useOnCurrentTargetChange(callback?: (isCurrent: boolean) => void) {
    const { register, deregister } = useContext(NavigationTargetContext)
    useEffect(() => {
        if (!callback) return
        register(callback)
        return () => deregister(callback)
        // callback should not change while previewing
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [register, deregister])
}
